export default {
    data() {
        return {
            msgContents: ["界面显示错乱", "启动缓慢，卡出翔了", "UI无法直视，丑哭了", "偶发性崩溃"],
            stars: [1, 2, 3, 4, 5],
            imageList: [],
            sendDate: {
                score: 0,
                content: "",
                contact: ""
            }
        }
    },
    onLoad() {
    },
    methods: {
        close(e){
            this.imageList.splice(e,1);
        },
        chooseMsg() { //快速输入
            uni.showActionSheet({
                itemList: this.msgContents,
                success: (res) => {
                    this.sendDate.content = this.msgContents[res.tapIndex];
                }
            })
        },
        chooseImg() { //选择图片
            uni.chooseImage({
                sourceType: ["camera", "album"],
                sizeType: "compressed",
                count: 8 - this.imageList.length,
                success: (res) => {
                    this.imageList = this.imageList.concat(res.tempFilePaths);
                }
            })
        },
        chooseStar(e) { //点击评星
            this.sendDate.score = e;
        },
        previewImage() { //预览图片
            uni.previewImage({
                urls: this.imageList
            });
        },
        send() { //发送反馈
            console.log(JSON.stringify(this.sendDate));
            let imgs = this.imageList.map((value, index) => {
                return {
                    name: "image" + index,
                    uri: value
                }
            })
            uni.uploadFile({
                url: "https://service.dcloud.net.cn/feedback",
                files: imgs,
                formData: this.sendDate,
                success: (res) => {
                    if (res.statusCode === 200) {
                        uni.showToast({
                            title: "反馈成功!"
                        });
                        this.imageList = [];
                        this.sendDate = {
                            score: 0,
                            content: "",
                            contact: ""
                        }
                    }
                },
                fail: (res) => {
                    uni.showToast({
                        title: "失败",
                        icon:"none"
                    });
                    console.log(res)
                }
            });
        }
    }
}
